.navToggle .navBtn .upper,
.navToggle .navBtn .lower {
    transition: 0.2s ease;
    width: 30px;
    cursor: pointer;
    display: block;
    height: 2px;
    margin-top: 6px;
    background-color: #000;
}
.navBtn:hover > .upper {
    -webkit-transform: rotate(46deg);
            transform: rotate(46deg);
    position: relative;
    bottom: 1px;
    right: 1px;
    
}

.navBtn:hover > .lower {
    
    right: 1px;
    -webkit-transform: rotate(-49deg);
            transform: rotate(-49deg);
    position: relative;
    bottom: 9px;

}
